<template>
    <!--div class="container">
        <!-- 2 PAI, 3 CANTINA -->
        <el-row :class="{items: true, 'light-gray-bg': true}">
            <el-col :class="{item: true, 'silver-bg': true, 'xs-light-black-bg-h': true, 'light-gray': true}"><i class="material-icons">{{menu.op1.icon}}</i><p>{{menu.op1.texto}}</p></el-col>
            <el-col :class="{item: true, 'silver-bg': true, 'xs-light-black-bg-h': true, 'light-gray': true}"><i class="material-icons">{{menu.op2.icon}}</i><p>{{menu.op2.texto}}</p></el-col>
            <el-col :class="{item: true, 'silver-bg': true, 'xs-light-black-bg-h': true, 'light-gray': true}"><i class="material-icons">{{menu.op3.icon}}</i><p>{{menu.op3.texto}}</p></el-col>
        </el-row>
    <!--/div-->
</template>
<script>
    export default {
        props: ['nivel'],
        data() {
            return {
                menu: '',
                cantina: {
                    op1: {
                        texto: 'PEDIDOS',
                        link: '',
                        icon: ''
                    },
                    op2: {
                        texto: 'ESTOQUE',
                        link: '',
                        icon: ''
                    },
                    op3: {
                        texto: 'OPÇÕES',
                        link: '',
                        icon: 'settings'
                    }
                },
                pais: {
                    op1: {
                        texto: 'LANCHES',
                        link: '',
                        icon: 'local_pizza'
                    },
                    op2: {
                        texto: 'REFEIÇÃO',
                        link: '',
                        icon: 'restaurant'
                    },
                    op3: {
                        texto: 'OPÇÕES',
                        link: '',
                        icon: 'settings'
                    }
                }
            }
        },
        created() {
            if (this.nivel == 2) {
                this.menu = this.cantina;
            } else if (this.nivel == 3) {
                this.menu = this.pais;
            } else {
                this.menu = 'n tem nada';
            }
        }
    }
</script>
<style scoped>
    .items {
        display: flex;
        flex-direction: column;
        height: 100vh;
        /*width: inherit;*/
        justify-content: center;
        align-content: flex-end;
    }

    .item {
        display: flex;
        /*background-color: purple;*/
        height: 20vh;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    @media screen and (min-width: 769px) {
        .item:not(:last-child) {
            margin-bottom: 2vh;
        }
    }

/*
    .item:hover {
        @extend .silver-bg;
    }
    */

    .container {
        height: inherit;
        background-color: #3366aa;
        align-items: center;
    }
    @media screen and (max-width: 768px) {
        .container {
            flex-direction: column-reverse;
        }
        .item {
            height: 15vh;
            width: 32vw;
        }
        .item:not(:last-child) {
            margin-right: 2vw;
        }
        .items {
            flex-direction: row;
            height: auto;
        }
    }
    @media screen and (max-width: 300px) {
        .item {
            font-size: 0.75rem;
        }
    }
</style>


