<template>
    <div class="home dark-white-bg">
        <el-row>
            <el-col :span="6" :lg="{span: 4}" :md="{span: 5}" :sm="{span: 4}" :class="{menu: true, 'light-gray-bg': true}" >
            <component :is="menu" :nivel="nivel"></component>
            </el-col>
        </el-row>
    </div>
</template>
<script>
    import Menu from './Menu';

    export default {
        data() {
            return {
                menu: Menu,
                nivel: window.sessionStorage.getItem('nivel')
            }
        },
        components: {
            Menu
        }
    }
</script>
<style scoped>
    .home {
        height: 100vh;
        width: 100vw;
    }
    el-row {
        background-color: orange;
    }
    .menu {
        height: 100vh;
    }

    #t {
        background-color: purple;
    }

    @media (max-width: 768px) {
        .menu {
            display: flex;
            width: 100vw;
            height: 15vh;
            justify-content: center;
            
        }
        .home {
            display: flex;
            flex-direction: column-reverse;
        }
    }
</style>


